<template>
  <div>
    <div class="bg_center">
      <h1>main page</h1>
    </div>

    <div>
    <b-carousel
      id="carousel-1"
      v-model="slide"
      :interval="4000"
      controls
      indicators
      background="#ababab"
      img-width="1024"
      img-height="480"
      style="text-shadow: 1px 1px 2px #333;"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
    >
      <!-- Text slides with image -->
      <!-- img-src="../assets/bali2.jpg" -->
      <!-- img-src="https://picsum.photos/1024/480/?image=52" -->
      <b-carousel-slide
        caption="Taiwan"
        text=""
        img-src="../assets/taiwan.jpg"
      ></b-carousel-slide>

      <b-carousel-slide
        caption="Huangshan"
        text=""
        img-src="../assets/huangshan.jpg"
      ></b-carousel-slide>

      <b-carousel-slide img-src="../assets/okinawa.jpg"></b-carousel-slide>

    </b-carousel>

    <p class="mt-4">
      Slide #: {{ slide }}<br>
      Sliding: {{ sliding }}
    </p>
  </div>


  </div>
</template>


<script>
export default {
  name: 'Main',
  props: [],
  components: {
  },

  methods: {
    onSlideStart(slide) {
      this.sliding = true
    },
    onSlideEnd(slide) {
      this.sliding = false
    }
  },

  data () {
    return {
      slide: 0,
      sliding: null
    }
  },
}
</script>

<style>
</style>


